.about{
    padding-top: 120px;
    padding-bottom: 140px;
    position: relative;
    background-color: #e9f5f9;
    &__header{
        h2{
            font-size: 1.5rem;
            margin-bottom: 0;
            margin-bottom: 3.5rem;
        }
    }
    &__wrapper{
        background: #fff;
        position: relative;
        &-content{
            padding-top: 90px;
            padding-bottom: 90px;
        }
    }
    &__item{
        
        &-title{
            font-size: 50px;
            font-weight: 700;
            color: var(--c-title);
            margin-bottom: 0;
            &-container{
                display: flex;
                align-items: center;
                justify-content: center;
                height: 100%;
            }
        }
        &-subtitle{
            font-size: var(--fz-l);
            color: var(--c-title);
            margin-bottom: 50px;
            line-height: 1.2;
        }
        &-right{
            p{
                font-size: var(--fz-s);
                color: var(--c-def);
                margin-bottom: 80px;
                max-width: 75%;
            }
        }
    }
}